* {
  font-family: Helvetica, Arial, sans-serif;
}

body {
  background-color: #1f1f1f;
  color: #e0e0e0;
  padding: 60px;
}

input {
  color: #e9e9e9;
  background-color: #111111;
  border:2px #e0e0e0;
  border-radius: 8px;
  font-size: 1.2rem;
  width: 100%;
}

.card-grid {
  display: flex;
  margin-left: auto;
  margin-right: auto;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid #e0e0e0;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p {
  padding: 5px;
  text-align: center;
  text-transform: capitalize;
}

.card img{
  display: flexbox;
  margin-left: auto;
  margin-right: auto;
}